import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

import Container from 'components/ui/Container';
import Button from 'components/ui/Button';
import TitleSection from 'components/ui/TitleSection';
import Airform from 'react-airform';
import InfoBlock from 'components/ui/InfoBlock';


import * as Styled from './styles';

const ContactInfo = () => {
  const { markdownRemark, allMarkdownRemark } = useStaticQuery(graphql`
    query {
      markdownRemark(frontmatter: { category: { eq: "contact section" } }) {
        frontmatter {
          title
          subtitle
        }
      }
      allMarkdownRemark(filter: { frontmatter: { category: { eq: "contact" } } }, sort: { fields: fileAbsolutePath }) {
        edges {
          node {
            id
            frontmatter {
              title
              icon
              content
            }
          }
        }
      }
    }
  `);

  const sectionTitle = markdownRemark.frontmatter;
  const contacts = allMarkdownRemark.edges;

  return (
    <Styled.ContactInfo>
      <Container section>
        <TitleSection title={sectionTitle.title} subtitle={sectionTitle.subtitle} center />
        <Styled.Form>
        <i>If you need any advice or collaboration, please do not hesitate to write to and I will get back to you soonest possible.</i>
        <br/><br/>
          <Airform email="contact@muhammadsyafrudin.com">
            <Styled.Input type="text" name="name" placeholder="Your name" required/>
            <Styled.Input type="email" name="email" placeholder="Your email" required/>
            <Styled.Input type="text" name="subject" placeholder="Subject/Topic"/>
            <Styled.Textarea name="message" placeholder="Your message" required/>
            <Button primary block>Send</Button>
          </Airform>
          <br/>
          <i>You may also email me at udin[at]dongguk[dot]edu or send a <a target='_blank' alt='link' rel='noopener noreferrer' href='https://open.kakao.com/o/s1qqvptc'>private message</a>.</i>
          {contacts.map((item) => {
            const {
              frontmatter: { title, icon, content }
            } = item.node;

            return (
                <InfoBlock icon={icon} title={title} content={content} center />
            );
          })}
        </Styled.Form>
      </Container>
    </Styled.ContactInfo>
  );
};

export default ContactInfo;
